/* Cart */
  
.cart {
    display: flex;
    flex-wrap: wrap;
    margin: 1rem;
    align-items: flex-start;
  }
  .cart-list {
    flex: 3 1 60rem;
  }
  .cart-action {
    flex: 1 1 20rem;
    background-color: #f0f0f0;
    border-radius: 0.5rem;
    padding: 1rem;
  }
  .cart-list-container {
    padding: 0;
    list-style-type: none;
    padding: 1rem;
  }
  .cart-list-container li {
    display: flex;
    justify-content: space-between;
  
    padding-bottom: 1rem;
    margin-bottom: 1rem;
    border-bottom: 0.1rem #c0c0c0 solid;
  }
  .cart-list-container li img {
    max-width: 10rem;
    max-height: 10rem;
  }
  .cart-list-container li:first-child {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  .cart-image {
    flex: 1 1;
  }
  .cart-name {
    flex: 8 1;
  }
  .cart-price {
    flex: 1 1;
    font-size: 2.5rem;
    text-align: right;
  }

  .button-cart{
    padding: 1rem;
    border: 0.1rem #808080 solid;
    border-radius: 0.5rem;
    cursor: pointer;
    background-color: #f0c040;
    width: 20rem;
    text-align: center;
  }