/* Place Order */
  
.placeorder {
    display: flex;
    flex-wrap: wrap;
    padding: 1rem;
    justify-content: space-between;
  }
  .placeorder-info {
    flex: 3 1 60rem;
  }
  .placeorder-action {
    flex: 1 1 20rem;
    border: 0.1rem #c0c0c0 solid;
    border-radius: 0.5rem;
    background-color: #fcfcfc;
    padding: 1rem;
  }
  .placeorder-info > div {
    border: 0.1rem #c0c0c0 solid;
    border-radius: 0.5rem;
    background-color: #fcfcfc;
    padding: 1rem;
    margin: 1rem;
  }
  .placeorder-info > div:first-child {
    margin-top: 0;
  }
  .placeorder-action > ul {
    padding: 0;
    list-style-type: none;
  }
  .placeorder-action > ul > li {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
  }
  .button-order{
    padding: 1rem;
    border: 0.1rem #808080 solid;
    border-radius: 0.5rem;
    cursor: pointer;
    background-color: #f0c040;
    width: 40rem;
    text-align: center;
  }
